import { tr } from "@/services/lang";
export default {
  layout: "index",
  head() {
    return {
      title: "صفحه تک محصول",
      meta: [
        {
          hid: "description",
          name: "description",
          content: "محصول x این ویژگی ها را دارد",
        },
      ],
    };
  },
  async asyncData({ error, route, $axios }) {
    try {
      const res = await $axios.get('product/' + route.params.store_slug + '/' + route.params.products_id);
      if (res.data) {
        return {
          product: res.data.data,
        };
      } else {
        return {
          product: null,
        };
      }
    } catch (e) {
      console.log(e);
      if (e.response) {
        error({
          statusCode: e.response.status,
          message: e.response.message,
        });
      } else {
        error({
          statusCode: "",
          message: "خطا در ارتباط",
        });
      }
    }
  },
  computed: {
    lang() {
      return tr();
    },
  },
  data() {
    return {
      product: null,
      options: {
        loop: false,
        paginationEnabled: true,
      },
      per_page: 1,
    };
  },
  mounted() {},
  methods: {
    add_item() {
      this.$store.dispatch("products/addProductToCart", {
        id: 1,
        name: "پیراهن",
        price: 200000,
        img: "",
      });
    },
  },
};
